body {
  margin: 0;
  padding: 0;
}

.header {
  background: transparent;
  position: relative;
  width: 100%;
  z-index: 3;
}

.header__list {
  background: #81b3d2;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  min-height: 56px;
  margin: 0;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0;
}

.header__list-item {
  width: 100%;
  text-align: center;
  padding-top: 17px;
  padding-bottom: 12px;
  justify-content: center;
  display: flex;
}

.header__item-link {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
}

.header__item-link:hover {
  color: #81B3D2;
}

.header__item-link:active {
  color: #000000;
}

.header__logo {
  display: flex;
  justify-content: center;
  width: 100%;
}

.header__logo img {
  width: 185px;
  height: 96px;
}

.header__wrapper {
  margin-bottom: -39px;
}

.header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
  display: block;
  width: 70px;
  height: 51px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.header--closed .header__toggle::before {
  content: "";
  position: absolute;
  top: 20px;
  left: 28px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}

.header--opened .header__toggle {
  top: 0;
  right: auto;
  left: 0;
}
.header--opened .header__toggle::before, .header--opened .header__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.header--opened .header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header--opened .header__toggle::after {
  transform: rotate(-45deg);
}
.header--opened .header__toggle:active::before, .header--opened .header__toggle:active::after {
  background-color: fade(#ffffff, 30%);
}

.header--closed .header__list {
  display: none;
}

@media (min-width: 768px) {
  .header {
    position: static;
  }

  .header__logo {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }

  .header__list-items-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  .header--closed .header__list {
    display: flex;
    flex-direction: row;
    gap: 153px;
  }

  .header__list {
    flex-direction: row;
    justify-content: space-between;
    background: transparent;
    position: static;
  }

  .header__item-link {
    color: #000000;
    font-size: 14px;
  }

  .header__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header--closed .header__list {
    gap: 241px;
  }

  .header__list {
    position: relative;
    z-index: 20;
  }
}
