@import "global/variables.scss";

.realtown {
  background: #81b3d2;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: -5px;
}

.realtown__title {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  padding-top: 33px;
}

.realtown__number {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  line-height: 21px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 11px;
  margin-bottom: 12px;
}

.realtown__description {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #ffffff;
  padding-bottom: 40px;
  margin: 0 auto;
  max-width: 262px;
}

.realtown__image {
  width: 100%;
  height: 260px;
  object-fit: cover;
  display: block;
}

@media (min-width: $tablet-width) {
  .realtown {
    display: grid;
    grid-template-columns: 1fr 2fr;
    height: 256px;
    overflow: hidden;
  }

  .realtown__image {
    width: 100%;
    height: 256px;
    object-fit: cover;
  }

  .realtown__title {
    font-size: 21px;
    max-width: 190px;
    text-align: center;
    margin: 0 auto;
    padding-top: 53px;
  }

  .realtown__number {
    margin-top: 13px;
  }

  .realtown__description {
    max-width: 190px;
    margin: 0 auto;
    text-align: center;
    margin-top: 23px;
  }
}

@media (min-width: $desktop-width) {
  .realtown__description {
    max-width: 275px;
  }
}
